import React from "react";
import Layout from "../components/layout";
import { Country } from "country-state-city";
import { initializeApp } from "firebase/app";
import { collection, addDoc, getFirestore } from "firebase/firestore/lite";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { useStaticQuery, graphql } from "gatsby";
import { utils } from "../core/helper";
import * as Yup from "yup";

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Required"),
  content: Yup.string().label("Content").required(),
});

const Blog = (props) => {
  const { pageContext } = props;
  console.log("pageContext::", pageContext);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const countries = Country.getAllCountries();
  const app = initializeApp(
    pageContext?.firestore?.data?.site?.siteMetadata?.firestore
  );
  const db = getFirestore(app);

  const handleOnSubmit = async (values) => {
    const clients = collection(db, "clients");
    await addDoc(clients, values);
    utils.showSuccssMsg("We will contact/talk to you soon");
    reset();
  };

  return (
    <Layout db={pageContext?.firestore?.data?.site?.siteMetadata?.firestore}>
      <section className="mt-5">
        <div className="container">
          <div className="row ">
            <div className="col-md-12 col-lg-8">
              <div className="row">
                <div className="blog_img shadow">
                  <img src={pageContext?.data?.image} alt="blog-img" />
                </div>
                <h2 className="mt-4 fs-1 mb-0">{pageContext?.data?.title}</h2>
                <div className="blog_wrapper mb-3">
                  <h4 className="fs-2 mb-3 mt-3 text-dark">
                    {pageContext?.data?.author_name}
                  </h4>
                  <i className="fs-4 text-secondary">
                    Posted On: ({pageContext?.data?.blog_created_date})
                  </i>
                </div>
                <div
                  dangerouslySetInnerHTML={{
                    __html: pageContext?.data?.description,
                  }}
                  className="blog-content mt-3"
                ></div>
              </div>
            </div>
            <div className="col-md-12 col-lg-4">
              <div className="card sticky-top section-cta border-0 contact_us_card">
                <div className="card-header contact_us-header border-0 bg-transparent d-flex">
                  <h4 className="my-auto">Contact Us</h4>
                </div>
                <div className="card-body">
                  <form
                    onSubmit={handleSubmit(handleOnSubmit)}
                    className="cta-form"
                  >
                    <div>
                      <label htmlFor="full-name" className="text-dark">
                        Name
                      </label>
                      <input
                        id="full-name"
                        type="text"
                        placeholder="Enter Your Name"
                        {...register("name")}
                        maxLength={30}
                      />
                      <p className="text-danger mb-0">{errors.name?.message}</p>
                    </div>
                    <div>
                      <label htmlFor="email" className="text-dark">
                        Email
                      </label>
                      <input
                        id="email"
                        type="email"
                        placeholder="me@example.com"
                        {...register("email")}
                        maxLength={50}
                      />
                      <p className="text-danger mb-0">
                        {errors.email?.message}
                      </p>
                    </div>
                    <div>
                      <label htmlFor="select-where" className="text-dark">
                        Country
                      </label>
                      <select name="country" {...register("country")}>
                        <option value="">Please choose one option:</option>
                        {countries.map((ele, ind) => {
                          return (
                            <option key={ind} value={ele.isoCode}>
                              {ele.name}
                            </option>
                          );
                        })}
                      </select>
                      <p className="text-danger mb-0">
                        {errors.country?.message}
                      </p>
                    </div>
                    <div>
                      <label htmlFor="mobile" className="text-dark">
                        Skype / Whatsapp / Mobile
                      </label>
                      <input
                        id="mobile"
                        type="tel"
                        placeholder="Skype / Whatsapp / Mobile"
                        {...register("mobile")}
                        maxLength={50}
                      />
                      <p className="text-danger mb-0">
                        {errors.mobile?.message}
                      </p>
                    </div>
                    <div>
                      <label htmlFor="content" className="text-dark">
                        Content
                      </label>
                      <textarea
                        id="content"
                        placeholder="Type content here"
                        rows={5}
                        className="input"
                        {...register("content")}
                        maxLength={200}
                      ></textarea>
                      <p className="text-danger mb-0">
                        {errors.content?.message}
                      </p>
                    </div>
                    <button className="btn btn--form mt-3 mx-0 p-3">
                      Submit
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default Blog;
